<template>
  <div id="step2">

    <div>
      <p class="flow-text center-align">Lets check for a provider in your area.</p>
      <p class="flow-text center-align">&nbsp;</p>
    </div>

    <div class="progress">
      <div class="determinate" style="width:66%"></div>
    </div>

    <form @submit.prevent="processStep">

      <div class="input-field">
        <label for="address">Street address</label>
        <input type="text" name="address" id="address" class="validate">
      </div>

      <div class="input-field">
        <label for="city">City</label>
        <input type="text" name="city" id="city" class="validate">
      </div>

      <div class="input-field">
        <label for="state">State</label>
        <input type="text" name="state" id="state" class="validate">
      </div>

      <div class="input-field">
        <label for="zipcode">zipcode</label>
        <input type="tel" name="zipcode" id="zipcode" value="12345">
      </div>

      <div class="input-field center-align">
        <button type="submit" class="btn waves-effect waves-teal">Next</button>
      </div>

    </form>

  </div>  
</template>

<script>
export default {
  name: 'step2',
  data () {
    return {
    }
  },
  mounted () {
    window.Materialize.updateTextFields()
  },
  methods: {
    processStep () {
      this.$router.push({ name: 'step3' })
    }
  }
}
</script>

<style>
</style>



